html{
font-size:  50%;
}
body{
  background-color: darkgray;
}
.wrap{
  width: 1000px;
  margin: auto;
  margin-top: 100px;
}
.card{
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.6);
  transition: 0.4s;
  width: 300px;
  background: #fff;
  text-align: center;
  font-size: 16px;
  font-family: sans-serif;
  float: left;
  margin: 10px;
  cursor: pointer;
  }
  img{
    width: 300px;
    height: 200px;
  }
  .main{
    float: left;
    margin: 5px;
    text-align: center;
  }
  div {width:100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .machtitle{

    color: blue;
    background-color:cadetblue;
    opacity: 50%;
    text-align: center;
  }
  .machresult{
    color: crimson;
    text-align: center;
    background-color:darksalmon;
    width: 50%;
    margin: auto;
  }
  h1{
    font-style: italic;
    font-family:cursive ;
    font-size: 10rem;
  
}
input{
  text-align: center;
}
h3{
  font-size: 60px;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  color: rgba(0,0,0,0.6);
}
h2{
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-size: 35px;
  color:midnightblue;
  
}
  .show{
    display: grid;grid-template-columns: 1fr 15fr 1fr;
  }
  button{
    padding: 12px 20px;
    background: brown;
    color: cornsilk;
    border: 20px solid #fff;
    outline: none;
    border-radius: 5px;
    text-transform: uppercase;
    cursor: pointer;
  }
  #head{
    display: flex;
    flex-direction: row;
  }